.content {
  margin: 24px 24px 0;
}
.content .link {
  margin-top: 16px;
}
.content .link:not(:empty) {
  margin-bottom: 16px;
}
.content .link a {
  margin-right: 32px;
  height: 24px;
  line-height: 24px;
  display: inline-block;
}
.content .link a i {
  font-size: 24px;
  margin-right: 8px;
  vertical-align: middle;
}
.content .link a span {
  height: 24px;
  line-height: 24px;
  display: inline-block;
  vertical-align: middle;
}
.page-menu-search {
  text-align: center;
  margin-bottom: 16px;
}
.page-menu-tabs {
  margin-top: 48px;
}
.extra-img {
  margin-top: -60px;
  text-align: center;
  width: 195px;
}
.extra-img img {
  width: 100%;
}
.mobile .extra-img {
  margin-top: 0;
  text-align: center;
  width: 96px;
}
.mobile .extra-img img {
  width: 100%;
}
